section.board {
@include stdFlex(row, nowrap, flex-end, flex-end);

    flex-grow: 1;
    padding: 15vh 0;
    width: 100%;
    
    background: $colorMainLight;
    border: $widthPlayer/4 solid $colorSecondary;
    overflow: hidden;
    position: relative;

    border-bottom-left-radius: $widthPlayer;
    border-bottom-right-radius: $widthPlayer;
    box-shadow: 0 0 20px -2px $colorSecondary inset;

    .particles {
        position: absolute;
        left: 0;
        top: 0;

        height: 100%;
        width: 100%;
    }

    .start-stop {
        position: absolute;
        left: 50%;
        top: 50%;

        border: none;
        outline: none;
    }

    .result {
        position: absolute;
        left: 50%;
        top: 50%;

        transform: translateX(-50%) translateY(-50%);
        color: $colorSecondary;
        font-size: 44px;

        &::before {
            content: "SCORE";
            position: absolute;
            top: -50%;
            left: 50%;

            transform: translateX(-50%) translateY(-50%);
            color: $colorSecondary;
            font-size: 32px;
        }
    }
}